  '2740': {
    id: '2740',
    department: '',
    tags: [ '', ' ', ' ANOVA' ],
    name: '',
    credit: 3,
    teacher: '',
    time: "{'1': ['A', 'C'], '2': [], '3': [], '4': [], '5': [], '6': []}",
    location: '',
    semester: '108-1',
    description: '<p>1. Basic ideas of statistics, review of hypothesis testing, tests of normality and detection of outliers, tests of single proportion and the equality of two proportions<!--?xml:namespace prefix = o ns = "urn:schemas-microsoft-com:office:office" /--></p><p>2. Nonparametric or distribution free tests: differences between parametric and nonparametric tests; Mann-Whitney U test, Wilcoxons matched pairs signed-rank test</p><p>3. Power: the concept of type I error, type II error and power; how to compute power for z tests; factors that affect power; power curves for 1-tailed z test and 2-tailed z test; computation of sample sizes; power tables</p><p>4. Escalation of type I errors when conducting multiple tests</p><p>5. Introduction to one way ANOVA: why test variances when the null concerns only means; basic concepts about sum of squares and degrees of freedom; SS between, SS within, SS total; mean squares, F test, follow-up test</p><p>6. Fishers conceptualization of ANOVA, further concepts about sum of squares</p><p>7. Linear model approach to ANOVA</p><p>8. Planned comparisons: linear contrasts; degree of freedom of contrast; test of contrasts; trend analysis</p><p>9. Follow-up tests, multiple comparison, multiplicity: Tukey test, Tukey-Kramer test; Scheffes test; Dunnetts test, Bonferronis procedure, Sidaks procedure, Holms procedure</p><p>10. Two way ANOVA: factorial design; main effects; interaction effect; meaning of interaction; test of interaction and interpretation; simple main effect; fixed model; random model; mixed model</p><p>11. Repeated measures: one way repeated measures; the meaning and test of sphericity; adjustment of the degrees of freedom by means of the Huynh-Feldt approach and the Greenhouse-Geisser approach; introduction to the multivariate approach, ANOVA approach to reliability of test instrument</p><p>12. Analysis of covariance: basic assumptions, rationale, and interpretation of printout, Johnson-Neyman approach</p>'
  },
